* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  list-style: none;
  padding: 0;
}
.country-list li {
  margin-bottom: 10px;
}
img {
  margin-right: 10px;
}
.country-name {
  font-weight: 600;
}
.names-of-quality {
  font-weight: 700;
  margin-right: 5px;
}
.country-name-one {
  font-size: 40px;
  font-weight: 700;
}
